import React from "react";
import Paper from "@material-ui/core/Paper";
import LockIcon from "@material-ui/icons/Lock";
import Typography from "@material-ui/core/Typography";
import { RadioGroup, RadioButton } from "react-radio-buttons";
import Button from "@material-ui/core/Button";
import ReactRadioButtonGroup from "react-radio-button-group";

export default class Question extends React.Component {
  componentDidUpdate() {}
  render() {
    const choices = this.props.quest.choices.map(choice => {
      return (
        <RadioButton
          rootColor={"#000"}
          pointColor={"rgb(249, 89, 219)"}
          value={choice}
        >
          {choice}
        </RadioButton>
      );
    });
    console.log(this.props.quest.answer);
    return (
      <div>
        <Paper
          style={{
            padding: "15px",
            background: this.props.quest.answer == null ? "#fff" : "#7eda7e"
          }}
        >
          {/* <Typography variant="headline" component="h3">
           {this.props.quest.question}
          </Typography> */}
          <Typography component="p" style={{}}>
            <strong>{this.props.index + 1}. </strong>
            {this.props.quest.question}
          </Typography>
        </Paper>
        <RadioGroup
          value={this.props.quest.answer}
          onChange={ans => this.props.handleAnswerChange(ans)}
        >
          {choices}
        </RadioGroup>
      </div>
    );
  }
}
