import withRoot from './modules/withRoot';
// --- Post bootstrap -----
import React from 'react';
import PropTypes from 'prop-types';
import compose from 'recompose/compose';
import { withStyles } from '@material-ui/core/styles';
import {Link, Redirect} from 'react-router-dom';
import Typography from './modules/components/Typography';
import AppFooter from './modules/views/AppFooter';
import AppForm from './modules/views/AppForm';
import Button from '@material-ui/core/Button';
import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";

import axios from "axios";
import config from "./../config";
import MySnackbarContentWrapper from "./Alert";
import Snackbar from "@material-ui/core/Snackbar";

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

const styles = theme => ({
  form: {
    marginTop: theme.spacing.unit * 6,
  },
  button: {
    marginTop: theme.spacing.unit * 3,
    marginBottom: theme.spacing.unit * 2,
    marginLeft: theme.spacing.unit * 1
  },
  feedback: {
    marginTop: theme.spacing.unit * 2,
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit
  }
});

class SignIn extends React.Component {
  state = {
    open: false,
    openDialog: false,
    sent: false,
    phoneNumber: '',
    password: '',
    notification: { type: "", msg: "", open: false }
  };

  handleSubmit = () => {
    let phoneNumber = this.state.phoneNumber;
    let password = this.state.password;

    if (
      phoneNumber === "" ||
      password === "" 
    ) {
      this.setState({
        notification: { type: "error", msg: "Checkout All Fields" }
      });
      return this.handleOpen();
    }
    if (phoneNumber.toString().length !== 10 || phoneNumber.toString()[0] < 6) {
      this.setState({
        notification: {
          type: "error",
          msg: "Please enter a valid phone number"
        }
      });
      return this.handleOpen();
    }

    axios
      .post(`${config.host}/signin`, {
        phoneNumber,
        password
      })
      .then(res => {
        if (res.status === 200) {
          this.setState({
            notification: { type: "success", msg: "You are loggedin successfully!" }
          });
          localStorage.setItem("token", res.data.token);
          setTimeout(
            () => {
              this.setState({ redirect: true });
            },
            3000,
            this
          );
          return this.handleOpen();
        }
      })
      .catch(err => {
        console.log(err);
        if (err.response) {
          if (err.response.status === 422) {
            this.setState({
              notification: { type: "error", msg: err.response.data.result }
            });
            return this.handleOpen();
          }
        }
      });
    };
    handleOpen() {
      this.setState({ open: true });
      setTimeout(
        () => {
          this.setState({ open: false });
        },
        6000,
        this
      );
    }
    handleClose=()=> {
      this.setState({ open: false, openDialog: false });
    }
    
    handleClickOpen = () => {
      this.setState({ openDialog: true });
    };


  render() {
    const { classes } = this.props;
    if (this.state.redirect) {
      return <Redirect to="/rules" />;
    }
    return (
      <React.Fragment>
        <Snackbar
          anchorOrigin={{
            vertical: "top",
            horizontal: "center"
          }}
          open={this.state.open}
          autoHideDuration={6000}
          onClose={this.handleClose.bind(this)}
        >
          <MySnackbarContentWrapper
            style={{
              textAlign: "center",
              fontSize: "15px",
              fontWeight: "bold",
              fontFamily: "'Josefin Sans', sans-serif",
              paddingTop: "20px",
              paddingLeft: "20px",
              paddingRight: "20px"
            }}
            onClose={this.handleClose.bind(this)}
            variant={this.state.notification.type}
            message={this.state.notification.msg}
          />
        </Snackbar>
        <AppForm>
          <React.Fragment>
            <Typography variant="h3" gutterBottom marked="center" align="center">
              Log In
            </Typography>
            <Typography variant="body2" align="center">
              {'Not a member yet? '}
              <Link to={"/register"} align="center" underline="always">
                Register here
              </Link>
            </Typography>
          </React.Fragment>
            <form>
              <TextField
                id="outlined-phoneNumber"
                label="Phone Number"
                fullWidth
                type="text"
                className={classes.textField}
                value={this.state.phoneNumber}
                onChange={e =>
                  this.setState({ phoneNumber: e.target.value })
                }
                margin="normal"
                variant="outlined"
              />
              <TextField
                id="outlined-password"
                label="Password"
                fullWidth
                type="password"
                className={classes.textField}
                value={this.state.password}
                onChange={e => this.setState({ password: e.target.value })}
                margin="normal"
                variant="outlined"
              />
              <Button
                className={classes.button}
                size="large"
                color="secondary"
                fullWidth
                onClick={this.handleSubmit}
                style={{backgroundColor: '#ff3366', padding: '3%', color: '#fff'}}
              >
                {'Sign In'}
              </Button>
            </form>
            <Typography align="center">
              <Button onClick={this.handleClickOpen}>
                Forgot password?
              </Button>
            </Typography>
            <Dialog
            open={this.state.openDialog}
            onClose={this.handleClose}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
            maxWidth="sm"
            fullWidth
            >
            <DialogTitle id="alert-dialog-title" style={{textAlign: 'center'}}>
              Forgot Password
              <hr />
            </DialogTitle>
            <DialogContent>
              <DialogContentText id="alert-dialog-description">
              </DialogContentText>
                <TextField
                  id="outlined-email"
                  label="Enter you Email address"
                  fullWidth
                  autoFocus
                  type="text"
                  className={classes.textField}
                  value={this.state.email}
                  onChange={e =>
                    this.setState({ email: e.target.value })
                  }
                  margin="normal"
                  variant="outlined"
                />
            </DialogContent>
            <DialogActions style={{marginTop: '-3%'}}>
              <Grid container spacing={16}>
                <Grid item xs={12} sm={6}>
                  <Button
                    className={classes.button}
                    color="danger"
                    fullWidth
                    onClick={this.handleClose}
                    style={{backgroundColor: '#fff', border: '2px solid #444', padding: '3.5%', color: '#444'}}
                  >
                    {"Close"}
                  </Button>
                </Grid>
                <Grid item xs={12} sm={6}>
                  <Button
                    className={classes.button}
                    color="success"
                    fullWidth
                    onClick={this.handleSubmit}
                    style={{backgroundColor: '#ff3366', borderColor: '2px solid #ff3366', padding: '4%', color: '#fff'}}
                  >
                    {"Submit"}
                  </Button>
                </Grid>
              </Grid>
            </DialogActions>
          </Dialog>
          </AppForm>
        <AppFooter />
      </React.Fragment>
    );
  }
}

SignIn.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default compose(
  withRoot,
  withStyles(styles),
)(SignIn);
