import React from "react";
import Question from "./Question";
import axios from "axios";
import config from "../../config";

import Hidden from "@material-ui/core/Hidden";
import Grid from "@material-ui/core/Grid";
import LockIcon from "@material-ui/icons/Lock";
import Typography from "@material-ui/core/Typography";
import FormLabel from "@material-ui/core/FormLabel";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import RadioGroup from "@material-ui/core/RadioGroup";
import Radio from "@material-ui/core/Radio";
import Paper from "@material-ui/core/Paper";
import Button from "@material-ui/core/Button";
import AddIcon from "@material-ui/icons/Add";
import millisec from "millisec";
import { withStyles } from "@material-ui/core/styles";
import { Redirect } from "react-router-dom";
import MySnackbarContentWrapper from "../Alert";
import Snackbar from "@material-ui/core/Snackbar";
const styles = theme => ({
  button: {
    margin: theme.spacing.unit
  },
  input: {
    display: "none"
  }
});

class SelfTest extends React.Component {
  constructor() {
    super();

    this.state = {
      index: 0,
      period: null,
      questions: [],
      currentQuest: {
        id: 1,
        question: "what is ur name ?",
        choices: ["kumar", "hello", "baskar", "ramki"],
        answer: null
      },
      answered: [],
      redirect: false,
      notification: { open: false, type: "", msg: "" }
    };

    var time = new Date();
    time.setMinutes(time.getMinutes() + 30);
    setInterval(() => {
      this.setState({ period: time - new Date() });
    }, 1000);
  }
  componentWillMount() {
    axios
      .get(`${config.host}/auth`, {
        headers: { authorization: localStorage.getItem("token") }
      })
      .then(res => {
        console.log(res);
        if (res.status == 200) {
          if (!res.data.started || res.data.phoneNumber == "9988998899") {
            this.getQuestions();
            return this.setState({
              user: res.data,
              started: res.data.started
            });
          } else {
            return this.setState({ redirect: true });
          }
        }
        return this.setState({ redirect: true });
      });
  }
  getQuestions() {
    axios
      .get(`${config.host}/questions`, {
        headers: { authorization: localStorage.getItem("token") }
      })
      .then(data => {
        console.log(data.data);
        this.setState({
          questions: data.data.result,
          currentQuest: data.data.result[0]
        });
      });
  }
  handleAnswerChange(ans) {
    let currentQuest = this.state.currentQuest;
    currentQuest.answer = ans;
    let Questions = this.state.questions;
    let answers = this.state.answered;
    // for (var i = 0; i < Questions.length; i++) {
    //   if (Questions[i].id == currentQuest.id) {
    //     Questions[i] = currentQuest;
    //   }
    // }
    let alreadyAnswered = false;
    this.state.questions.map((quest, i) => {
      if (quest.id == currentQuest.id) {
        return (Questions[i] = currentQuest);
      }
      return null;
    });
    this.state.answered.map((answer, i) => {
      if (answer.id == currentQuest.id) {
        alreadyAnswered = true;
        answers[i] = { id: currentQuest.id, answer: ans };
      }
    });
    if (!alreadyAnswered) {
      answers = [...answers, { id: currentQuest.id, answer: ans }];
    }
    this.setState({
      questions: Questions,
      currentQuest: currentQuest,
      answered: answers
    });
  }

  handleQuestionChange(i) {
    let quest = this.state.questions[i];
    this.setState({ currentQuest: quest, index: i });
  }
  handleClose() {
    this.setState({
      notification: { open: false, ...this.state.notification }
    });
  }
  handleSubmit = () => {
    axios
      .post(`${config.host}/submit`, this.state.answered, {
        headers: { authorization: localStorage.getItem("token") }
      })
      .then(res => {
        if (res.status == 200) {
          this.setState({
            redirect: true
          });
        }
      });
  };
  render() {
    if (this.state.redirect) {
      return <Redirect to="/rules" />;
    }
    const { classes } = this.props;
    const buttons = this.state.questions.map((val, i) => {
      let color = String;
      if (val.answer != null) {
        color = "#7eda7e";
      } else {
        color = "#f50057";
      }

      return (
        <Button
          onClick={this.handleQuestionChange.bind(this, i)}
          style={{
            background: color,
            fontSize: "12px",
            borderRadius: "0",
            margin: "5px",
            height: "35px",
            width: "35px",
            minHeight: "0"
          }}
          variant="fab"
          mini
          color="secondary"
          aria-label="add"
        >
          {i + 1}
        </Button>
      );
    });
    return (
      <div
        style={{
          flex: 1,
          display: "flex",
          alignContent: "center",
          justifyContent: "center",
          height: "80vh",
          margin: 20
        }}
      >
        <Grid container spacing={16}>
          <Grid xs={12} sm={9}>
            <Hidden mdUp>
              <div style={{ color: "#000", float: "right" }}>
                {millisec(this.state.period).format(" mm : ss")}{" "}
                <small>mins left</small>
              </div>
            </Hidden>
            <div style={{ marginTop: 50 }}>
              <Question
                quest={this.state.currentQuest}
                index={this.state.index}
                handleAnswerChange={this.handleAnswerChange.bind(this)}
              />
              <Button
                onClick={() =>
                  this.state.index == 0
                    ? null
                    : this.handleQuestionChange(this.state.index - 1)
                }
              >
                PREV
              </Button>
              <Button
                style={{ float: "right" }}
                onClick={() =>
                  this.state.index == this.state.questions.length - 1
                    ? null
                    : this.handleQuestionChange(this.state.index + 1)
                }
              >
                NEXT
              </Button>
            </div>
          </Grid>

          <Grid
            style={{ justifyContent: "center", padding: "0 20px" }}
            xs={12}
            sm={3}
            container
          >
            <div>
              <Grid xs={12}>
                <Hidden smDown>
                  <div style={{ color: "#000", marginTop: 50 }}>
                    {millisec(this.state.period).format(" mm : ss")}{" "}
                    <small>mins left</small>
                  </div>
                </Hidden>
                <h4 style={{ color: "#fff" }}>List Of Questions</h4>
                {buttons}
                <br />
                <br />
                <Button
                  style={{
                    background: "#7eda7e",
                    fontSize: "12px",
                    borderRadius: "0",
                    margin: "5px",
                    height: "15px",
                    width: "15px",
                    minHeight: "0"
                  }}
                  variant="fab"
                  mini
                  color="secondary"
                  aria-label="add"
                />
                <small style={{ color: "#000" }}>answered</small>
              </Grid>
            </div>
          </Grid>
          <Grid xs={12}>
            <Button
              style={{
                width: "25px",
                height: "25px",
                float: "right",
                backgroundColor: "rgba(151, 105, 245, 0.98)",
                color: "#fff"
              }}
              variant="contained"
              component="span"
              size="medium"
              color="primary"
              className={classes.button}
              onClick={this.handleSubmit.bind(this)}
            >
              Submit
            </Button>
          </Grid>
        </Grid>
      </div>
    );
  }
}

const WrappedSelfTest = withStyles(styles)(SelfTest);
export default WrappedSelfTest;
