import withRoot from "./modules/withRoot";
// --- Post bootstrap -----
import React from "react";
import PropTypes from "prop-types";
import compose from "recompose/compose";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import { Link, Redirect } from "react-router-dom";
import Typography from "./modules/components/Typography";
import AppFooter from "./modules/views/AppFooter";
import AppForm from "./modules/views/AppForm";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";

import axios from "axios";
import config from "./../config";
import MySnackbarContentWrapper from "./Alert";
import Snackbar from "@material-ui/core/Snackbar";

const styles = theme => ({
  form: {
    marginTop: theme.spacing.unit * 6
  },
  button: {
    marginTop: theme.spacing.unit * 3,
    marginBottom: theme.spacing.unit * 2,
    marginLeft: theme.spacing.unit * 1
  },
  feedback: {
    marginTop: theme.spacing.unit * 2
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit
  }
});

class SignUp extends React.Component {
  state = {
    sent: false,
    firstName: "",
    lastName: "",
    email: "",
    fathersName: "",
    phoneNumber: "",
    school: "",
    group: "",
    community: "",
    password: "",
    cpassword: "",
    registerNumber: "",
    notification: { type: "", msg: "", open: false }
  };

  validateEmail(email) {
    if (!email) return false;
    var x = email;
    var atpos = x.indexOf("@");
    var dotpos = x.lastIndexOf(".");
    if (atpos < 1 || dotpos < atpos + 2 || dotpos + 2 >= x.length) {
      return false;
    } else {
      return true;
    }
  }

  handleSubmit = () => {
    let firstName = this.state.firstName;
    let lastName = this.state.lastName;
    let email = this.state.email;
    let fathersName = this.state.fathersName;
    let phoneNumber = this.state.phoneNumber;
    let school = this.state.school;
    let group = this.state.group;
    let community = this.state.community;
    let password = this.state.password;
    let cpassword = this.state.cpassword;
    let registerNumber = this.state.registerNumber;

    if (
      firstName === "" ||
      lastName === "" ||
      email === "" ||
      phoneNumber === "" ||
      fathersName === "" ||
      password === "" ||
      cpassword === "" ||
      school === "" ||
      group === "" ||
      community === ""
    ) {
      this.setState({
        notification: { type: "error", msg: "Checkout All Fields" }
      });
      return this.handleOpen();
    }
    if (!this.validateEmail(email)) {
      this.setState({
        notification: {
          type: "error",
          msg: "Please enter a valid email address"
        }
      });
      return this.handleOpen();
    }
    if (phoneNumber.toString().length !== 10 || phoneNumber.toString()[0] < 6) {
      this.setState({
        notification: {
          type: "error",
          msg: "Please enter a valid phone number"
        }
      });
      return this.handleOpen();
    }
    if (password !== cpassword) {
      this.setState({
        notification: { type: "error", msg: "Passwords doesn't match" }
      });
      return this.handleOpen();
    }

    axios
      .post(`${config.host}/signup`, {
        firstName,
        lastName,
        email,
        phoneNumber,
        fathersName,
        password,
        school,
        group,
        community,
        registerNumber
      })
      .then(res => {
        if (res.status === 201) {
          this.setState({
            notification: {
              type: "success",
              msg: "You are registered successfully!"
            }
          });
          setTimeout(
            () => {
              this.setState({ redirect: true });
            },
            3000,
            this
          );
          return this.handleOpen();
        }
      })
      .catch(err => {
        console.log(err);
        if (err.response) {
          if (err.response.status === 422) {
            this.setState({
              notification: { type: "error", msg: err.response.data }
            });
            return this.handleOpen();
          }
        }else{
          this.setState({
            notification: { type: "error", msg:"Some Thing Went Wrong Please try again later" }
          });
          return this.handleOpen();
        }
      });
  };
  handleOpen() {
    this.setState({ open: true });
    setTimeout(
      () => {
        this.setState({ open: false });
      },
      6000,
      this
    );
  }
  handleClose() {
    this.setState({ open: false });
  }

  render() {
    const { classes } = this.props;
    if (this.state.redirect) {
      return <Redirect to="/signin" />;
    }
    return (
      <div>
        <Snackbar
          anchorOrigin={{
            vertical: "top",
            horizontal: "center"
          }}
          open={this.state.open}
          autoHideDuration={6000}
          onClose={this.handleClose.bind(this)}
        >
          <MySnackbarContentWrapper
            style={{
              textAlign: "center",
              fontSize: "15px",
              fontWeight: "bold",
              fontFamily: "'Josefin Sans', sans-serif",
              paddingTop: "20px",
              paddingLeft: "20px",
              paddingRight: "20px"
            }}
            onClose={this.handleClose.bind(this)}
            variant={this.state.notification.type}
            message={this.state.notification.msg}
          />
        </Snackbar>
        <React.Fragment>
          <AppForm>
            <React.Fragment>
              <Typography
                variant="h3"
                gutterBottom
                marked="center"
                align="center"
              >
                Register
              </Typography>
              <Typography
                variant="body2"
                align="center"
                style={{ marginBottom: "4%" }}
              >
                <Link
                  to={"/signin"}
                  underline="always"
                  style={{ color: "#333" }}
                >
                  Already have an account?
                </Link>
              </Typography>
            </React.Fragment>
            <form>
              <Grid container spacing={16}>
                <Grid item xs={12} sm={6}>
                  <TextField
                    id="outlined-firstname"
                    label="First Name"
                    fullWidth
                    type="text"
                    className={classes.textField}
                    value={this.state.firstName}
                    onChange={e => this.setState({ firstName: e.target.value })}
                    margin="normal"
                    variant="outlined"
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    id="outlined-lastName"
                    label=" Last Name"
                    fullWidth
                    type="text"
                    className={classes.textField}
                    value={this.state.lastName}
                    onChange={e => this.setState({ lastName: e.target.value })}
                    margin="normal"
                    variant="outlined"
                  />
                </Grid>
              </Grid>
              <TextField
                id="outlined-email"
                label="Email"
                fullWidth
                type="email"
                className={classes.textField}
                value={this.state.email}
                onChange={e => this.setState({ email: e.target.value })}
                margin="normal"
                variant="outlined"
              />
              <Grid container spacing={16}>
                <Grid item xs={12} sm={6}>
                  <TextField
                    id="outlined-phoneNumber"
                    label="Phone Number"
                    fullWidth
                    type="text"
                    className={classes.textField}
                    value={this.state.phoneNumber}
                    onChange={e =>
                      this.setState({ phoneNumber: e.target.value })
                    }
                    margin="normal"
                    variant="outlined"
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    id="outlined-fathersName"
                    label="Father's Name"
                    fullWidth
                    type="text"
                    className={classes.textField}
                    value={this.state.fathersName}
                    onChange={e =>
                      this.setState({ fathersName: e.target.value })
                    }
                    margin="normal"
                    variant="outlined"
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    id="outlined-school"
                    label="School"
                    fullWidth
                    type="text"
                    className={classes.textField}
                    value={this.state.school}
                    onChange={e => this.setState({ school: e.target.value })}
                    margin="normal"
                    variant="outlined"
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    id="outlined-school"
                    label="RegisterNUmber"
                    fullWidth
                    type="text"
                    className={classes.textField}
                    value={this.state.registerNumber}
                    onChange={e =>
                      this.setState({ registerNumber: e.target.value })
                    }
                    margin="normal"
                    variant="outlined"
                  />
                </Grid>
              </Grid>

              <Grid container spacing={16}>
                <Grid item xs={12} sm={6}>
                  <TextField
                    id="outlined-group"
                    label="12th Group"
                    fullWidth
                    type="text"
                    className={classes.textField}
                    value={this.state.group}
                    onChange={e => this.setState({ group: e.target.value })}
                    margin="normal"
                    variant="outlined"
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    id="outlined-community"
                    label="Community"
                    fullWidth
                    type="text"
                    className={classes.textField}
                    value={this.state.community}
                    onChange={e => this.setState({ community: e.target.value })}
                    margin="normal"
                    variant="outlined"
                  />
                </Grid>
              </Grid>
              <Grid container spacing={16}>
                <Grid item xs={12} sm={6}>
                  <TextField
                    id="outlined-password"
                    label="Password"
                    fullWidth
                    type="password"
                    className={classes.textField}
                    value={this.state.password}
                    onChange={e => this.setState({ password: e.target.value })}
                    margin="normal"
                    variant="outlined"
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    id="outlined-cpassword"
                    label="Confirm Password"
                    fullWidth
                    type="password"
                    className={classes.textField}
                    value={this.state.cpassword}
                    onChange={e => this.setState({ cpassword: e.target.value })}
                    margin="normal"
                    variant="outlined"
                  />
                </Grid>
              </Grid>
              <Grid container spacing={16}>
                <Grid item xs={12} sm={12}>
                  <Button
                    className={classes.button}
                    color="secondary"
                    fullWidth
                    onClick={this.handleSubmit}
                    style={{
                      backgroundColor: "#ff3366",
                      padding: "3%",
                      color: "#fff"
                    }}
                  >
                    {"Register"}
                  </Button>
                </Grid>
              </Grid>
            </form>
          </AppForm>
          <AppFooter />
        </React.Fragment>
      </div>
    );
  }
}

SignUp.propTypes = {
  classes: PropTypes.object.isRequired
};

export default compose(
  withRoot,
  withStyles(styles)
)(SignUp);
