import withRoot from "./modules/withRoot";
// --- Post bootstrap -----
import React from "react";
import Typography from "./modules/components/Typography";
import LayoutBody from "./modules/components/LayoutBody";
import AppFooter from "./modules/views/AppFooter";
import { Grid, withStyles } from "@material-ui/core";
import Whatshot from "@material-ui/icons/Whatshot";
import { Link, Redirect } from "react-router-dom";
import Button from "@material-ui/core/Button";
import config from "../config";
import axios from "axios";

const styles = theme => ({
  para: {
    fontSize: "20px"
  }
});

class Rules extends React.Component {
  state = { redirect: false, user: {}, started: false, to: "/test" };
  componentWillMount = () => {
    axios
      .get(`${config.host}/auth`, {
        headers: { authorization: localStorage.getItem("token") }
      })
      .then(res => {
        console.log(res);
        if (res.status == 200) {
          return this.setState({
            user: res.data,
            started: res.data.started
          });
        }
        return this.setState({ to: "/signin", redirect: true });
      })
      .catch(err => this.setState({ to: "/signin", redirect: true }));
  };
  render() {
    if (this.state.redirect) {
      return <Redirect to={this.state.to} />;
    }
    const { classes } = this.props;
    return (
      <React.Fragment>
        <LayoutBody margin marginBottom>
          <Typography variant="h3" gutterBottom marked="center" align="center">
            Rules
          </Typography>
          <Typography>
            <Grid container>
              {this.state.started &&
              this.state.user.phoneNumber != 9988998899 ? (
                "You have already written test"
              ) : (
                <Grid xs="12" md="12">
                  <p className={classes.para}>
                    <Whatshot /> You can write only once
                  </p>
                  <p className={classes.para}>
                    <Whatshot /> no more other tabs while writing test('should
                    ',
                  </p>

                  <div style={{ textAlign: "center", width: "100%" }}>
                    <Button
                      onClick={() => {
                        if (
                          window.confirm(
                            `U can write test only once, 
                             Are you sure you wish to continue Writing Test`
                          )
                        ) {
                          this.setState({ redirect: true });
                        }
                      }}
                    >
                      Enter Writing Test
                    </Button>
                  </div>
                </Grid>
              )}
            </Grid>
          </Typography>
        </LayoutBody>
        <AppFooter />
      </React.Fragment>
    );
  }
}

const WrappedComponent = withRoot(Rules);
export default withStyles(styles)(WrappedComponent);
